/*
 * Material Modal CSS
 */
.modal {
  will-change: visibility, opacity;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1); }
  .modal--active {
    visibility: visible;
    opacity: 1; }
  .modal--align-top {
    align-items: flex-start; }
  .modal__bg {
    background: rgba(20, 20, 20, 0.5); }
  .modal__dialog {
    max-width: 100%;
    padding: 1.2rem; }
  .modal__content {
    will-change: transform, opacity;
    position: relative;
    padding: 2.4rem;
    background-clip: padding-box;
    opacity: 0;
    transition: all 0.25s cubic-bezier(0.23, 1, 0.32, 1); }
    .modal__content--active {
      opacity: 1; }
    .modal__content h2 {
      margin-bottom: 0.6em; }
  .modal__close {
    z-index: 1100;
    cursor: pointer;
    background: gold; }
  .modal__button {
    position: relative;
    display: inline-block;
    padding: 1.6rem;
    color: rgba(0, 0, 0, 0.7);
    line-height: 1;
    cursor: pointer;
    background: gold;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);
    font-weight: 600;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    margin: 1em 0.4em 1em 0;
    font-size: 1rem; }
    .modal__button:hover {
      background: rgba(79, 69, 15, 0.55); }
    .modal__button:visited {
      color: black; }
  .modal .buttons {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }

#modal__temp {
  will-change: transform, opacity;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(20, 20, 20, 0.5);
  transform: none;
  opacity: 1;
  transition: opacity 0.1s ease-out, transform 0.3s cubic-bezier(0.23, 1, 0.32, 1); }

.demo-close {
  position: absolute;
  top: 0;
  right: 0;
  margin: 1.2rem;
  padding: 0.6rem;
  background: gold;
  border-radius: 50%;
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1); }
  .demo-close svg {
    width: 24px;
    fill: black;
    pointer-events: none;
    vertical-align: top; }
  .demo-close:hover {
    background: #ffeb80; }

